import React from 'react';
import {Link} from 'react-router';
import $ from 'jquery';

class Home extends React.Component{
  constructor(){
    super();
    this.state={
      data: []
    }
  }
  componentWillMount(){
    let _this = this;
    $.ajax({
      type: 'GET',
      url: 'https://cnodejs.org/api/v1/topics',
      success: function (response) {
        // console.log(response);
        _this.setState({data: response.data})
      },
      error: function () {
        alert('')
      }
    })
  }
  handleClick(type){
    let _this = this;
    $.ajax({
      url: `https://cnodejs.org/api/v1/topics?tab=${type}`,
      success: function (response) {
        _this.setState({data: response.data})
      }
    })
  }
  render(){
    return(
      <div>
        <button onClick={this.handleClick.bind(this,'all')}></button>
        <button onClick={this.handleClick.bind(this,'good')}></button>
        <button onClick={this.handleClick.bind(this,'share')}></button>
        <button onClick={this.handleClick.bind(this,'ask')}></button>
        <button onClick={this.handleClick.bind(this,'job')}></button>

        <div>
          {
            this.state.data.map( item =>
              <div key={item.id}>
                <Link to={`user/${item.author.loginname}`}><img src={item.author.avatar_url} alt='img' style={{width: '30px', height: '30px'}}/></Link>
                <span>{item.reply_count}/{item.visit_count} </span>
                <span style={{background:'#00bcd4'}}> {item.top? '' : item.tab}</span>
                <Link to={`/topic/${item.id}`}> {item.title}</Link>
              </div>
            )
          }
        </div>
      </div>
    )
  }
}

export default Home;



